import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { setNightTheme } from "../store/variables/action";
import { Brightness2, WbSunny } from '@material-ui/icons';

export default function Nav() {

    const { 
        variables: { nightTheme }
    } = useSelector(state => state);
    const dispatch = useDispatch();

    return (
        <nav className="flex justify-around py-6 text-lg">
            <div>Shivam</div>
            <ul className="flex">
                <li className="px-3">
                    <Link to="/">Home</Link>
                </li>
                <li className="px-3">
                    <Link to="/about">About</Link>
                </li>
                <li className="px-3">
                    <Link to="/skills">Skills</Link>
                </li>
                <li className="px-3">
                    <Link to="/contact">Contactme</Link>
                </li>
                <li onClick={() => dispatch(setNightTheme())} className="px-3">
                    {
                        nightTheme ? <Brightness2 /> : <WbSunny /> 
                    }
                </li>
            </ul>
        </nav>
    )
}
